import React from 'react'
import Head from 'next/head'
import { useState,useEffect } from 'react';

import Navbar from '../Component/Navbar'
import Sidebar from '../Component/Sidebar'
import Carosel from '../Component/Carousel'
import MovieList from '../Component/MovieList'
import Footer from '../Component/Footer'

import {getMoviesData} from '../Data/MovieData'


class Home extends React.Component {
   /*  const [movie , setMovies] = useState([]) */
   
    /* static getInitialProps(){

    } */

     constructor(props){
        super(props)
            this.state ={
                movie:[],
                errorMessage:''
         
            }       
    } 

   

  componentDidMount(){
   getMoviesData().then((movie)=>{
       this.setState({movie}) 
   })
   .catch((error)=>{
       this.setState({
           errorMessage:error
       })
   })
   } 

render(){
     const{ movie, errorMessage}=this.state 
    return( <div>

                <Head>
                    <title>Home</title>
                    <link rel="stylesheet" href="https://stackpath.bootstrapcdn.com/bootstrap/4.3.1/css/bootstrap.min.css" integrity="sha384-ggOyR0iXCbMQv3Xipma34MD+dH/1fQ784/j6cY/iJTQUOhcWr7x9JvoRxT2MZw1T" crossorigin="anonymous" />
                    <script src="https://code.jquery.com/jquery-3.3.1.slim.min.js" integrity="sha384-q8i/X+965DzO0rT7abK41JStQIAqVgRVzpbzo5smXKp4YfRvH+8abtTE1Pi6jizo" crossorigin="anonymous"></script>
                    <script src="https://cdnjs.cloudflare.com/ajax/libs/popper.js/1.14.7/umd/popper.min.js" integrity="sha384-UO2eT0CpHqdSJQ6hJty5KVphtPhzWj9WO1clHTMGa3JDZwrnQq4sF86dIHNDz0W1" crossorigin="anonymous"></script>
                    <script src="https://stackpath.bootstrapcdn.com/bootstrap/4.3.1/js/bootstrap.min.js" integrity="sha384-JjSmVgyd0p3pXB1rRibZUAYoIIy6OrQ6VrjIEaFf/nJGzIxFDsf4x0xIM+B07jRM" crossorigin="anonymous"></script>
                </Head>
       
                <Navbar/>
      
        <div className="container mt-5">
            <div className="row">
    
                <div className="col-lg-3">
                    <Sidebar/>
                </div>
    
                <div className="col-lg-9">
                    <Carosel/>
    
                    <div className="row">
                       {/*  {errorMessage && errorMessage} */}
                       {/*  <MovieList movie={props.movie}/> */}
                        <MovieList movie={this.state.movie } />
                    </div>
    
                </div>
            </div>
        </div>
    
    
        <Footer/>
      
    
      </div>
    )
}

/* Home.getInitialProps=async()=>{
    const movie = await getMoviesData()
    return{
        movie
    }
} */


}
export default Home
